/** @jsx jsx */
import React from "react"
import { Link, StaticQuery, graphql } from "gatsby"
import Img from "gatsby-image"
import { jsx, useThemeUI } from "theme-ui"
import { useShopProducts } from "StaticQueries/shop-products"
import getPaymentUrl from "Components/payments/qiwi"
import SideBar from "Project/_components/sidebar"
import { EnvelopeFill, XCircleFill } from "react-bootstrap-icons"

export default function IndexPage() {
  const context = useThemeUI()
  const { theme } = context
  const shopProductsData = useShopProducts()

  return (
    <>
      <main
        sx={{
          display: "flex",
          justifyContent: "column",
          width: "100%",
          height: "100%",
          "@media (max-width: 991.98px)": {
            header: {
              width: "100%",
            },
            footer: {
              width: "100%",
            },
            "#main-content": {
              paddingTop: "96px",
            },
          },
          "@media (max-width: 767.98px)": {
            ".product-list": {
              padding: "0px",
            },
            h2: {
              paddingX: "16px",
            },
          },
        }}
      >
        <SideBar />

        {/* MAIN */}
        <div id={"main-content"} sx={{ width: "100%" }}>
          <div
            sx={{
              display: "flex",
              flexDirection: "column",
              justifyContent: "center",
              alignItems: "center",
              height: "240px",
              userSelect: "none",
              marginBottom: "32px",
              position: "relative",
              h1: {
                color: "white",
                textAlign: "center",
              },
            }}
          >
            <div
              sx={{
                display: "flex",
                alignItems: "center",
                justifyContent: "center",
                paddingX: "24px",
                paddingY: "16px",
                backgroundColor: "rgba(0, 0, 0, 0.24)",
                backdropFilter: "blur(32px)",
                position: "absolute",
                zIndex: "1",
                "@media (max-width: 575.98px)": {
                  width: "100%",
                  height: "100%",
                },
              }}
            >
              <h1 sx={{ variant: ["text.large.h1"] }}>
                Аккаунты World of Tanks
              </h1>
            </div>
            <StaticQuery
              query={graphql`
                {
                  file(relativePath: { eq: "_assets/shop/shop-cover.jpg" }) {
                    childImageSharp {
                      fixed(width: 1600) {
                        ...GatsbyImageSharpFixed_withWebp
                      }
                    }
                  }
                }
              `}
              render={data => (
                <Img
                  fixed={data.file.childImageSharp.fixed}
                  style={{
                    display: "flex",
                    minHeight: "240px",
                    width: "100%",
                    position: "absolute !important",
                  }}
                />
              )}
            ></StaticQuery>
          </div>
          <div className={"product-list"} sx={{ padding: "16px" }}>
            <h2 sx={{ variant: ["text.large.h2"], marginBottom: "16px" }}>
              Каталог товаров
            </h2>
            <div
              className="table-responsive"
              sx={{
                table: {
                  thead: { userSelect: "none" },
                  tbody: {
                    boxShadow: `inset 1px 0px 0px ${theme.colors.separator_opaque},inset -1px 0px 0px ${theme.colors.separator_opaque},inset 0px 1px 0px ${theme.colors.separator_opaque},inset 0px -1px 0px ${theme.colors.separator_opaque}`,
                    tr: {
                      ":nth-of-type(odd)": {
                        backgroundColor: "fill_quarternary",
                      },
                    },
                  },
                  th: {
                    paddingX: "16px",
                    color: "text",
                    textTransform: "uppercase",
                    display: "table-cell",
                    fontWeight: "normal",
                    borderBottomWidth: "0px",
                    minHeight: "32px",
                    whiteSpace: "nowrap",
                  },
                  td: {
                    paddingX: "16px",
                    display: "table-cell",
                    color: "gray",
                    borderBottomWidth: "0px",
                    a: {
                      color: "gray",
                    },
                  },
                  tr: {
                    ":hover": {
                      ".table-buy-button": {
                        color: "white",
                        backgroundColor: "tint_blue",
                      },
                    },
                  },
                },
                "@media (max-width: 767.98px)": {
                  scrollbarWidth: "none",
                  msOverflowStyle: "none",
                  "::-webkit-scrollbar": {
                    display: "none",
                  },
                  table: {
                    width: "768px",
                    tbody: {
                      tr: {
                        ".table-buy-button": {
                          color: "white",
                          backgroundColor: "tint_blue",
                        },
                      },
                    },
                  },
                },
              }}
            >
              <table className="table">
                <thead>
                  <tr>
                    <th className={"element-small"} scope="col">
                      Товар
                    </th>
                    <th className={"element-small"} scope="col">
                      Наличие
                    </th>
                    <th className={"element-small"} colSpan="2">
                      Цена за 1 шт.
                    </th>
                  </tr>
                </thead>
                <tbody>
                  {shopProductsData.map(({ node }) => (
                    <tr key={node.id}>
                      <td className={"element-medium"} scope="col">
                        <Link to={node.slug}>{node.excerpt}</Link>
                      </td>
                      <td className={"element-medium"} scope="col">
                        {node.frontmatter.extra.amount}
                      </td>
                      <td
                        className={"element-medium"}
                        scope="col"
                        sx={{
                          paddingRight: "0px !important",
                          paddingY: "0px !important",
                        }}
                      >
                        <div
                          sx={{
                            display: "grid",
                            gridAutoFlow: "column",
                            justifyContent: "space-between",
                            alignItems: "center",
                          }}
                        >
                          <span>{node.frontmatter.extra.price}</span>
                          <button
                            data-toggle="modal"
                            data-target={`#${node.id}`}
                            className={
                              "element-medium element-button table-buy-button"
                            }
                            sx={{
                              color: "tint_blue",
                              marginLeft: "8px",
                              paddingX: "16px",
                            }}
                          >
                            Купить
                          </button>
                        </div>
                      </td>
                      <div
                        className="modal fade"
                        id={node.id}
                        tabIndex="-1"
                        aria-hidden="true"
                      >
                        <div className="modal-dialog modal-dialog-centered modal-sm">
                          <div className="modal-content">
                            <div
                              sx={{
                                backgroundColor: "background_primary",
                                borderRadius: `${theme.root.secondaryBorderRadius}`,
                              }}
                            >
                              <div
                                className={"element element-large"}
                                sx={{
                                  boxShadow: `inset 0px -1px 0px ${theme.colors.separator_opaque}`,
                                }}
                              >
                                <div className={"element-center-part"}>
                                  <span>Оформить заказ</span>
                                </div>
                              </div>
                              <div
                                sx={{
                                  //paddingX: "16px",
                                  paddingY: "24px",
                                  display: "flex",
                                  flexDirection: "column",
                                }}
                              >
                                <div className={`element element-medium`}>
                                  <div className={"element-left-part"}>
                                    <input
                                      placeholder="Ваш email"
                                      className={`element element-medium element-input input-filled`}
                                      sx={{ paddingX: "16px" }}
                                    />
                                  </div>
                                </div>
                                <div sx={{ marginTop: "24px" }}>
                                  <div
                                    className={"element element-small"}
                                    sx={{
                                      textTransform: "uppercase",
                                      color: "gray",
                                      boxShadow: `0px 1px 0px ${theme.colors.separator_opaque}`,
                                    }}
                                  >
                                    <div className={"element-left-part"}>
                                      <span>Способ оплаты</span>
                                    </div>
                                  </div>

                                  <div className={`element element-medium`}>
                                    <div className={"element-left-part"}>
                                      <input
                                        class="form-check-input"
                                        type="checkbox"
                                        value=""
                                        id="flexCheckChecked"
                                        checked
                                      ></input>
                                      <label
                                        class="form-check-label"
                                        for="flexCheckChecked"
                                        sx={{
                                          marginLeft: "8px",
                                          color: "text",
                                        }}
                                      >
                                        Банковская карта или Qiwi
                                      </label>
                                    </div>
                                  </div>
                                </div>
                              </div>
                              <div
                                sx={{
                                  display: "flex",

                                  boxShadow: `inset 0px 1px 0px ${theme.colors.separator_opaque}`,
                                }}
                              >
                                <button
                                  type="button"
                                  data-dismiss="modal"
                                  aria-label="Close"
                                  className={
                                    "element element-large element-button"
                                  }
                                  sx={{
                                    width: "100%",
                                    justifyContent: "center",
                                    color: "tint_blue",
                                    paddingX: "16px",
                                    boxShadow: `inset -1px 0px 0px ${theme.colors.separator_opaque}`,
                                  }}
                                >
                                  Отмена
                                </button>
                                <button
                                  className={
                                    "element element-large element-button"
                                  }
                                  onClick={event => {
                                    const paymentInfo = {
                                      amount: node.frontmatter.extra.price,
                                      email: `gjlk@gmail.com`,
                                      comment: `clientCommentHere`,
                                    }

                                    location.href = getPaymentUrl(paymentInfo)
                                  }}
                                  sx={{
                                    width: "100%",
                                    justifyContent: "center",
                                    paddingX: "16px",
                                    color: "tint_blue",
                                    fontWeight: "bold",
                                    ":hover": {
                                      backgroundColor: "transparent",
                                    },
                                  }}
                                >
                                  Оплатить
                                </button>
                              </div>
                            </div>
                          </div>
                        </div>
                      </div>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          </div>
          <div
            sx={{ display: "flex", marginTop: "48px", minHeight: "52px" }}
          ></div>
        </div>
      </main>
    </>
  )
}
