import { useStaticQuery, graphql } from "gatsby"

export const useShopProducts = () => {
  const { allMdx } = useStaticQuery(
    graphql`
      query shopProducts {
        allMdx(filter: { slug: { regex: "/shop//" } }) {
          edges {
            node {
              slug
              id
              body
              excerpt
              frontmatter {
                title
                categories
                tags
                extra {
                  price
                  amount
                }
              }
            }
          }
        }
      }
    `
  )
  return allMdx.edges
}
